"use client"

import { useState, useEffect } from "react"
import { ChevronLeft, ChevronRight, Clock, Route, Save, MapPin } from "lucide-react"
import { useRouter, useSearchParams } from "next/navigation"
import { saveMonthSchedule, getMonthSchedule, getDateCollections } from "@/lib/firebase"
import type { DaySchedule, MonthSchedule } from "@/lib/types"

export default function ScheduleManagement() {
  const router = useRouter()
  const searchParams = useSearchParams()
  const [selectedMonth, setSelectedMonth] = useState(new Date())
  const [schedules, setSchedules] = useState<MonthSchedule>({})
  const [selectedDate, setSelectedDate] = useState<string | null>(null)
  const [saving, setSaving] = useState(false)
  const [message, setMessage] = useState("")
  const [availableRoutes, setAvailableRoutes] = useState<string[]>([])

  useEffect(() => {
    loadSchedule()
    loadAvailableRoutes()
  }, [selectedMonth])

  async function loadSchedule() {
    const yearMonth = `${selectedMonth.getFullYear()}-${String(selectedMonth.getMonth() + 1).padStart(2, "0")}`
    const schedule = await getMonthSchedule(yearMonth)
    if (schedule) {
      setSchedules(schedule)
    } else {
      setSchedules({})
    }
  }

  async function loadAvailableRoutes() {
    const routes = await getDateCollections()
    setAvailableRoutes(routes)
  }

  async function handleSaveSchedule() {
    setSaving(true)
    setMessage("")

    const yearMonth = `${selectedMonth.getFullYear()}-${String(selectedMonth.getMonth() + 1).padStart(2, "0")}`
    const success = await saveMonthSchedule(yearMonth, schedules)

    if (success) {
      setMessage("スケジュールを保存しました")
    } else {
      setMessage("保存に失敗しました")
    }

    setSaving(false)
    setTimeout(() => setMessage(""), 3000)
  }

  function getDaysInMonth(date: Date) {
    const year = date.getFullYear()
    const month = date.getMonth()
    const firstDay = new Date(year, month, 1)
    const lastDay = new Date(year, month + 1, 0)
    const days = []

    for (let day = 1; day <= lastDay.getDate(); day++) {
      const dateStr = `${year}-${String(month + 1).padStart(2, "0")}-${String(day).padStart(2, "0")}`
      days.push({ day, dateStr })
    }

    return { days, firstDayOfWeek: firstDay.getDay() }
  }

  const { days, firstDayOfWeek } = getDaysInMonth(selectedMonth)

  function handlePreviousMonth() {
    setSelectedMonth(new Date(selectedMonth.getFullYear(), selectedMonth.getMonth() - 1))
    setSelectedDate(null)
  }

  function handleNextMonth() {
    setSelectedMonth(new Date(selectedMonth.getFullYear(), selectedMonth.getMonth() + 1))
    setSelectedDate(null)
  }

  function handleDayClick(dateStr: string) {
    setSelectedDate(dateStr)
  }

  function updateSchedule(dateStr: string, updates: Partial<DaySchedule>) {
    setSchedules((prev) => ({
      ...prev,
      [dateStr]: { ...prev[dateStr], ...updates } as DaySchedule,
    }))
  }

  function toggleOperating(dateStr: string) {
    const current = schedules[dateStr]
    if (current?.isOperating) {
      const newSchedules = { ...schedules }
      delete newSchedules[dateStr]
      setSchedules(newSchedules)
    } else {
      updateSchedule(dateStr, {
        isOperating: true,
        startTime: "10:00",
        endTime: "11:30",
        routeType: "循環ルート",
        routeId: availableRoutes[0] || undefined,
        startLocation: "コア前",
      })
    }
  }

  useEffect(() => {
    const selectedRouteId = searchParams.get("selectedRoute")
    const dateParam = searchParams.get("date")

    if (selectedRouteId && dateParam) {
      updateSchedule(dateParam, { routeId: selectedRouteId })
      // URLパラメータをクリア
      router.replace("/operator/schedule")
    }
  }, [searchParams])

  function handleSelectRouteFromMap() {
    if (!selectedDate) return
    router.push(`/operator/route?selectMode=true&date=${selectedDate}`)
  }

  const monthName = selectedMonth.toLocaleDateString("ja-JP", { year: "numeric", month: "long" })
  const selectedSchedule = selectedDate ? schedules[selectedDate] : null

  return (
    <div style={{ width: "100%", height: "100%", backgroundColor: "#f3f4f6", overflowY: "auto", padding: 16 }}>
      <div style={{ maxWidth: 900, margin: "0 auto" }}>
        {/* Header */}
        <div
          style={{
            backgroundColor: "white",
            borderRadius: 16,
            padding: 24,
            marginBottom: 16,
            boxShadow: "0 1px 3px 0 rgba(0, 0, 0, 0.1)",
          }}
        >
          <div style={{ display: "flex", justifyContent: "space-between", alignItems: "start", marginBottom: 8 }}>
            <div>
              <h1 style={{ fontSize: 24, fontWeight: 700, color: "#1f2937", marginBottom: 8 }}>スケジュール設定</h1>
              <p style={{ color: "#6b7280", fontSize: 14 }}>
                カレンダーから日付を選択して、運行スケジュールを設定してください
              </p>
            </div>
          </div>
        </div>

        {/* Message */}
        {message && (
          <div
            style={{
              backgroundColor: "rgba(16, 185, 129, 0.1)",
              border: "1px solid #10b981",
              color: "#065f46",
              padding: "12px 16px",
              borderRadius: 8,
              marginBottom: 16,
              fontSize: 14,
              fontWeight: 600,
            }}
          >
            {message}
          </div>
        )}

        {/* Calendar */}
        <div
          style={{
            backgroundColor: "white",
            borderRadius: 16,
            padding: "16px",
            marginBottom: 16,
            boxShadow: "0 1px 3px 0 rgba(0, 0, 0, 0.1)",
          }}
        >
          {/* Month Navigation */}
          <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between", marginBottom: 16 }}>
            <button
              onClick={handlePreviousMonth}
              style={{
                width: 36,
                height: 36,
                borderRadius: 8,
                border: "1px solid #e5e7eb",
                backgroundColor: "white",
                cursor: "pointer",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <ChevronLeft size={20} />
            </button>
            <h2 style={{ fontSize: 18, fontWeight: 700, color: "#1f2937" }}>{monthName}</h2>
            <button
              onClick={handleNextMonth}
              style={{
                width: 36,
                height: 36,
                borderRadius: 8,
                border: "1px solid #e5e7eb",
                backgroundColor: "white",
                cursor: "pointer",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <ChevronRight size={20} />
            </button>
          </div>

          {/* Weekday Headers */}
          <div style={{ display: "grid", gridTemplateColumns: "repeat(7, 1fr)", gap: 4, marginBottom: 4 }}>
            {["日", "月", "火", "水", "木", "金", "土"].map((day, index) => (
              <div
                key={day}
                style={{
                  textAlign: "center",
                  fontSize: 11,
                  fontWeight: 600,
                  color: index === 0 ? "#ef4444" : index === 6 ? "#3b82f6" : "#6b7280",
                  padding: 4,
                }}
              >
                {day}
              </div>
            ))}
          </div>

          {/* Calendar Grid */}
          <div style={{ display: "grid", gridTemplateColumns: "repeat(7, 1fr)", gap: 4 }}>
            {Array.from({ length: firstDayOfWeek }).map((_, index) => (
              <div key={`empty-${index}`} />
            ))}

            {days.map(({ day, dateStr }) => {
              const schedule = schedules[dateStr]
              const isOperating = schedule?.isOperating
              const isSelected = selectedDate === dateStr

              return (
                <button
                  key={dateStr}
                  onClick={() => handleDayClick(dateStr)}
                  style={{
                    aspectRatio: "1",
                    borderRadius: 8,
                    border: isSelected ? "2px solid #3b82f6" : "1px solid #e5e7eb",
                    backgroundColor: isOperating ? "#dbeafe" : "white",
                    cursor: "pointer",
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    justifyContent: "center",
                    padding: 4,
                    position: "relative",
                    transition: "all 0.2s",
                  }}
                >
                  <div style={{ fontSize: 14, fontWeight: 600, color: isOperating ? "#1e40af" : "#1f2937" }}>{day}</div>
                </button>
              )
            })}
          </div>
        </div>

        {/* Schedule Details */}
        {selectedDate && (
          <div
            style={{
              backgroundColor: "white",
              borderRadius: 16,
              padding: 24,
              boxShadow: "0 1px 3px 0 rgba(0, 0, 0, 0.1)",
              marginBottom: 80,
            }}
          >
            <h3 style={{ fontSize: 18, fontWeight: 700, color: "#1f2937", marginBottom: 16 }}>{selectedDate}の設定</h3>

            {/* Operating Toggle */}
            <div style={{ marginBottom: 24 }}>
              <label style={{ display: "flex", alignItems: "center", cursor: "pointer" }}>
                <input
                  type="checkbox"
                  checked={selectedSchedule?.isOperating || false}
                  onChange={() => toggleOperating(selectedDate)}
                  style={{ width: 20, height: 20, marginRight: 12, cursor: "pointer" }}
                />
                <span style={{ fontSize: 16, fontWeight: 600, color: "#1f2937" }}>この日は運行する</span>
              </label>
            </div>

            {selectedSchedule?.isOperating && (
              <>
                {/* Time Range */}
                <div style={{ marginBottom: 24 }}>
                  <label style={{ display: "block", fontSize: 14, fontWeight: 600, color: "#374151", marginBottom: 8 }}>
                    <Clock size={16} style={{ display: "inline", marginRight: 8 }} />
                    運行時間
                  </label>
                  <div style={{ display: "flex", alignItems: "center", gap: 12 }}>
                    <input
                      type="time"
                      value={selectedSchedule.startTime}
                      onChange={(e) => updateSchedule(selectedDate, { startTime: e.target.value })}
                      style={{
                        flex: 1,
                        padding: "12px 16px",
                        border: "1px solid #d1d5db",
                        borderRadius: 8,
                        fontSize: 16,
                      }}
                    />
                    <span style={{ color: "#6b7280" }}>〜</span>
                    <input
                      type="time"
                      value={selectedSchedule.endTime}
                      onChange={(e) => updateSchedule(selectedDate, { endTime: e.target.value })}
                      style={{
                        flex: 1,
                        padding: "12px 16px",
                        border: "1px solid #d1d5db",
                        borderRadius: 8,
                        fontSize: 16,
                      }}
                    />
                  </div>
                </div>

                {/* Route Type */}
                <div style={{ marginBottom: 24 }}>
                  <label style={{ display: "block", fontSize: 14, fontWeight: 600, color: "#374151", marginBottom: 8 }}>
                    <Route size={16} style={{ display: "inline", marginRight: 8 }} />
                    ルートタイプ
                  </label>
                  <div style={{ display: "flex", gap: 12 }}>
                    <button
                      onClick={() => updateSchedule(selectedDate, { routeType: "循環ルート" })}
                      style={{
                        flex: 1,
                        padding: "12px 16px",
                        border: selectedSchedule.routeType === "循環ルート" ? "2px solid #3b82f6" : "1px solid #d1d5db",
                        borderRadius: 8,
                        backgroundColor: selectedSchedule.routeType === "循環ルート" ? "#dbeafe" : "white",
                        cursor: "pointer",
                        fontSize: 16,
                        fontWeight: 600,
                        color: selectedSchedule.routeType === "循環ルート" ? "#1e40af" : "#6b7280",
                        transition: "all 0.2s",
                      }}
                    >
                      循環ルート
                    </button>
                    <button
                      onClick={() => updateSchedule(selectedDate, { routeType: "フリー運行" })}
                      style={{
                        flex: 1,
                        padding: "12px 16px",
                        border: selectedSchedule.routeType === "フリー運行" ? "2px solid #3b82f6" : "1px solid #d1d5db",
                        borderRadius: 8,
                        backgroundColor: selectedSchedule.routeType === "フリー運行" ? "#dbeafe" : "white",
                        cursor: "pointer",
                        fontSize: 16,
                        fontWeight: 600,
                        color: selectedSchedule.routeType === "フリー運行" ? "#1e40af" : "#6b7280",
                        transition: "all 0.2s",
                      }}
                    >
                      フリー運行
                    </button>
                  </div>
                </div>

                {/* Route Selection */}
                {selectedSchedule.routeType === "循環ルート" && (
                  <div style={{ marginBottom: 24 }}>
                    <label
                      style={{ display: "block", fontSize: 14, fontWeight: 600, color: "#374151", marginBottom: 8 }}
                    >
                      <Route size={16} style={{ display: "inline", marginRight: 8 }} />
                      使用するルート
                    </label>

                    <div style={{ display: "flex", gap: 12 }}>
                      <button
                        onClick={handleSelectRouteFromMap}
                        style={{
                          flex: 1,
                          padding: "16px 20px",
                          border: "2px dashed #3b82f6",
                          borderRadius: 12,
                          backgroundColor: "white",
                          cursor: "pointer",
                          fontSize: 16,
                          fontWeight: 600,
                          color: "#3b82f6",
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "center",
                          gap: 8,
                          transition: "all 0.2s",
                        }}
                      >
                        <MapPin size={20} />
                        地図でルートを選択
                      </button>
                    </div>

                    {selectedSchedule.routeId && (
                      <div
                        style={{
                          marginTop: 12,
                          padding: "12px 16px",
                          backgroundColor: "#dbeafe",
                          borderRadius: 8,
                          border: "1px solid #3b82f6",
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "space-between",
                        }}
                      >
                        <div>
                          <div style={{ fontSize: 12, color: "#6b7280", marginBottom: 4 }}>選択中のルート</div>
                          <div style={{ fontSize: 16, fontWeight: 700, color: "#1e40af" }}>
                            {selectedSchedule.routeId}
                          </div>
                        </div>
                        <button
                          onClick={() => updateSchedule(selectedDate, { routeId: undefined })}
                          style={{
                            padding: "6px 12px",
                            backgroundColor: "white",
                            border: "1px solid #d1d5db",
                            borderRadius: 6,
                            fontSize: 12,
                            color: "#6b7280",
                            cursor: "pointer",
                          }}
                        >
                          解除
                        </button>
                      </div>
                    )}
                  </div>
                )}

                {/* Start Location */}
                <div style={{ marginBottom: 24 }}>
                  <label style={{ display: "block", fontSize: 14, fontWeight: 600, color: "#374151", marginBottom: 8 }}>
                    <MapPin size={16} style={{ display: "inline", marginRight: 8 }} />
                    スタート地点
                  </label>
                  <input
                    type="text"
                    value={selectedSchedule.startLocation || ""}
                    onChange={(e) => updateSchedule(selectedDate, { startLocation: e.target.value })}
                    placeholder="例：コア前"
                    style={{
                      width: "100%",
                      padding: "12px 16px",
                      border: "1px solid #d1d5db",
                      borderRadius: 8,
                      fontSize: 16,
                    }}
                  />
                </div>

                {/* Save Button */}
                <button
                  onClick={handleSaveSchedule}
                  disabled={saving}
                  style={{
                    width: "100%",
                    padding: "16px 24px",
                    backgroundColor: saving ? "#9ca3af" : "#10b981",
                    color: "white",
                    border: "none",
                    borderRadius: 12,
                    fontSize: 16,
                    fontWeight: 700,
                    cursor: saving ? "not-allowed" : "pointer",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    gap: 8,
                    transition: "all 0.2s",
                  }}
                >
                  <Save size={20} />
                  {saving ? "保存中..." : "スケジュールを保存"}
                </button>
              </>
            )}
          </div>
        )}
      </div>
    </div>
  )
}
